import React, { useState } from 'react';
import JSZip from 'jszip';
import './styles/app.css';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';

const MySwal = withReactContent(Swal);

export default function App() {
  const [error] = useState(null);
  const [folderFiles, setFolderFiles] = useState([]);
  const [convertButtonText, setConvertButtonText] = useState('Convert');
  const [downloadButtonText, setDownloadButtonText] = useState('Download');

  const fetchWithTimeout = (url, options, timeout = 15000) => {
    return Promise.race([
      fetch(url, options),
      new Promise((_, reject) =>
        setTimeout(() => reject(new Error('Request timed out')), timeout)
      )
    ]);
  };

  const handleFolderChange = (event) => {
    const files = Array.from(event.target.files);
    setFolderFiles(files);
  };

  const handleSubmit = async () => {
    const zip = new JSZip();

    // Add files to the zip object with folder structure
    folderFiles.forEach((file) => {
      zip.file(file.webkitRelativePath, file);
    });

    try {
      // Generate the zip file
      const zipBlob = await zip.generateAsync({ type: 'blob' });

      const formData = new FormData();
      formData.append('folder', zipBlob, 'folder.zip'); // Name of the zip file

      const response = await fetch('http://localhost:5001/upload-zip', {
        method: 'POST',
        body: formData,
      });

      if (response.ok) {
        MySwal.fire({
          title: 'Success!',
          text: 'Project has been uploaded successfully',
          icon: 'success',
          timer: 3000,
          confirmButtonText: 'OK',
        });
      } else {
        const errorText = await response.text();
        alert(`Error uploading zipped folder: ${errorText}`);
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Error zipping or uploading folder');
    }
  };

  const handleConvertAndZip = async () => {
    try {
      // Set the converting button text
      setConvertButtonText('Converting...');

      const convertResponse = await fetch('http://localhost:5001/run-convert', {
        method: 'POST',
      });

      if (!convertResponse.ok) {
        const errorText = await convertResponse.text();
        console.log('Error running convert script: ' + errorText);
        console.log(convertResponse.status);
        setConvertButtonText('Convert'); // Reset button text on error
        return;
      }

      const zipResponse = await fetch('http://localhost:5001/zip-results', {
        method: 'GET',
      });

      if (!zipResponse.ok) {
        alert('Error zipping the results folder');
        setConvertButtonText('Converting'); // Reset button text on error
        return;
      }
      MySwal.fire({
        title: 'Success!',
        text: 'Convert successfully. Zipping the folder now...',
        icon: 'success',
        timer: 3000,
        confirmButtonText: 'OK',
      });
      setConvertButtonText('Convert');
    } catch (error) {
      console.error('Error:', error);
      alert('Error during the process');
    }
  };

    // Function to handle zipping and downloading
    const handleZipAndDownload = async () => {
      try {
      // Set the downloading button text
      setDownloadButtonText('Downloading...');

        const downloadResponse = await fetchWithTimeout('http://localhost:5001/download-zip', {
          method: 'GET',
        }, 10000); // Set a 10-second timeout
  
        if (!downloadResponse.ok) {
          throw new Error('Error downloading the zip file');
        }
  
        const blob = await downloadResponse.blob();
        const downloadUrl = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = downloadUrl;
        a.download = 'results.zip'; // Adjust the file name as needed
        document.body.appendChild(a);
        a.click();
        a.remove();
      } catch (error) {
        console.error('Error:', error);
        alert('Error during the process: ' + error.message);
      } finally {
        // Reset the button text back to normal
        setDownloadButtonText('Download');
      }
      MySwal.fire({
        title: 'Success!',
        text: 'Downloading your project...',
        icon: 'success',
        timer: 3000,
        confirmButtonText: 'OK',
      });
      setDownloadButtonText("Download");
    };

  return (
    <div className="App">
      <header className="header">
        <div className="logo">
          <img src="/assets/logo_transformer.png" alt="Logo" className="logo-image" />
          <span className="logo-text">Transformer</span>
        </div>
        {/* <button className="premium-button">Try Premium</button> */}
      </header>

      <main>
        <section className="converter-section">
          <div className="converter-container">
            <div className="logo-container">
              <img className="logo-image" src="/assets/java.svg" alt="Java logo" />
            </div>
            <h2 className="converter-title">Java to TypeScript Converter</h2>
            <div className="logo-container">
              <img className="logo-image" src="/assets//typescript.svg" alt="TypeScript logo" />
            </div>
          </div>

          <div className="container">
            <form id="converter-form">
              <div className="form-group">
                {/* <div className="textarea-container"> */}
                  <div style={{
                    display: "flex",
                    justifyContent: 'center',
                  }}>
                    <div className="upload-container" style={{
                      width: "50%",
                    }}>
                      <input
                        type="file"
                        webkitdirectory="true"
                        directory="true"
                        multiple
                        onChange={handleFolderChange}
                      />
                      <button
                        onClick={handleSubmit}>
                          Upload Project
                      </button>
                  </div>
                </div>
              </div>

              <div className="center-container">
                <button 
                  type= 'button'
                  id="convert-btn"
                  className= "submit-button"
                  onClick={handleConvertAndZip} disabled={convertButtonText !== 'Convert'
                }>
                  {convertButtonText}
                </button>
                <button 
                  type= 'button'
                  id="download-btn"
                  className= "submit-button"
                  onClick={handleZipAndDownload} disabled={downloadButtonText !== 'Download'
                }>
                  {downloadButtonText}
                </button>
              </div>
            </form>

            {/* Show error message if there's an error */}
            {error && <div className="error-message">{error}</div>}

            <section className="key-differences">
              <h3>Key differences between Java and TypeScript</h3>
              <table className="info-panel_table">
                <thead>
                  <tr>
                    <th>Characteristic</th>
                    <th>Java</th>
                    <th>TypeScript</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>Syntax</td>
                    <td>Java has a verbose syntax with a lot of boilerplate code.</td>
                    <td>TypeScript has a more concise and modern syntax.</td>
                  </tr>
                  <tr>
                    <td>Paradigm</td>
                    <td>Java is an object-oriented programming language that supports imperative and declarative paradigms.</td>
                    <td>TypeScript is a superset of JavaScript that supports object-oriented and functional programming paradigms.</td>
                  </tr>
                  <tr>
                    <td>Typing</td>
                    <td>Java is statically typed with type checking at compile-time.</td>
                    <td>TypeScript is statically typed with optional type annotations.</td>
                  </tr>
                  <tr>
                    <td>Performance</td>
                    <td>Java is known for high performance and scalability.</td>
                    <td>TypeScript's performance depends on the underlying JavaScript engine.</td>
                  </tr>
                  <tr>
                    <td>Libraries and frameworks</td>
                    <td>Java has a vast ecosystem of libraries and frameworks.</td>
                    <td>TypeScript has access to the same libraries and frameworks as JavaScript.</td>
                  </tr>
                  <tr>
                    <td>Community and support</td>
                    <td>Java has a large community with extensive documentation and support.</td>
                    <td>TypeScript has a growing community with good documentation and support.</td>
                  </tr>
                  <tr>
                    <td>Learning curve</td>
                    <td>Java has a steep learning curve due to its verbose syntax and complex concepts.</td>
                    <td>TypeScript has a relatively low learning curve due to its modern syntax and similarity to JavaScript.</td>
                  </tr>
                </tbody>
              </table>
            </section>
          </div>
        </section>
      </main>

      <footer className="footer">
        <p>&copy; 2024 Transformer. All rights reserved.</p>
        <p>Contact us: <a href="mailto:info@mywebsite.com">info@mywebsite.com</a></p>
        <div className="social-media">
          <a href="https://facebook.com/mywebsite" target="_blank" rel="noopener noreferrer" className="social-icon facebook-icon">
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-facebook">
              <path d="M18 2a6 6 0 0 0-6 6v4H8v4h4v8h4v-8h4l1-4h-5V8a1 1 0 0 1 1-1h4v-4h-1z"/>
            </svg>
          </a>
          <a href="https://twitter.com/mywebsite" target="_blank" rel="noopener noreferrer" className="social-icon twitter-icon">
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-twitter">
              <path d="M23 3a10.1 10.1 0 0 1-2.9.8A4.6 4.6 0 0 0 22.4 2a9.2 9.2 0 0 1-2.9 1.1A4.6 4.6 0 0 0 16.6 2a4.6 4.6 0 0 0-4.6 4.6c0 .3 0 .6.1.9A13.2 13.2 0 0 1 2 3.7a4.6 4.6 0 0 0 1.4 6.2A4.6 4.6 0 0 1 1 9v.1a4.6 4.6 0 0 0 3.7 4.5A4.6 4.6 0 0 1 3 14a4.6 4.6 0 0 0 4.3 3.2A4.6 4.6 0 0 1 4 17v.1a4.6 4.6 0 0 0 3.7 4.5A4.6 4.6 0 0 1 4 23a4.6 4.6 0 0 0 4.3 3.2A13.2 13.2 0 0 1 1 20a13.2 13.2 0 0 1 7.3 2.2A4.6 4.6 0 0 0 10 20c4.7 0 8.8-3.8 8.8-8.8v-.4A4.6 4.6 0 0 0 20.5 11a4.6 4.6 0 0 1 1.5-3.1A10.1 10.1 0 0 0 23 3z"/>
            </svg>
          </a>
          <a href="https://linkedin.com/company/mywebsite" target="_blank" rel="noopener noreferrer" className="social-icon linkedin-icon">
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-linkedin">
              <path d="M16 8a6 6 0 0 0-6 6v4h4v-4h-2v-2h4v2h2v-2a6 6 0 0 0-6-6zm-8 4h2v8H8zm0-10v8H6V8h2zm8 0h2v8h-2V8zM6 4H4v2h2V4zm8 0h2v2h-2V4z"/>
            </svg>
          </a>
        </div>
      </footer>
    </div>
  );
}
